import { animate } from 'popmotion';
document.addEventListener("DOMContentLoaded", function() {
    const ball = document.querySelector(".ball");
    animate({
        from: "0px",
        to: "200px",
        repeat: Infinity,
        repeatType: "mirror",
        type: "spring",
        onUpdate(update) {
            ball.style.left = update;
        },
    });
});
